import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ContactForm from '../components/contactForm';

class Contact extends Component {

  componentDidMount = () => this.props.resetScroll()

  render() {
    return (
      <div className="wrapper">

        {/* TITLECARD */}
        <div className="titleCardWrapper">
          {/* <h1 className="workNr mainColor">01</h1> */}
          <div className="titleCard">
            <h1>Contact</h1>
            <div className="bar"></div>
            <div className="shortInfo">
              <div className="row">
                <p className="title">E-mail</p>
                <p className="info">contact@kyliangieles.nl</p>
              </div>
              <div className="row">
                <p className="title">Tel. nummer</p>
                <p className="info">06 128 298 96</p>
              </div>
              <div className="row">
                <p className="title">Instagram</p>
                <p className="info">@kyliangieles</p>
              </div>
            </div>
            <p className="paragraph col5">Heb je iets gezien dat je interesse heeft gewekt? Schroom niet om contact met me op te nemen! Ik ben te bereiken via het contactformulier en de kanalen hierboven. Ik reageer doorgaans binnen 24 uur.</p>
            {/* <a className="cv" download="KylianGieles_CV.pdf" href={CV} >Download CV</a> */}
          </div>
        </div>

        {/* ENDCARD */}
        <div className="endCard">
          <div>
            <h1>Let's talk!</h1>
            <div className="bar"></div>
            {/* <Link to="/work">Klik hier voor mijn werk</Link> */}
          </div>
          <ContactForm />
        </div>
      </div>
    );
  }
}

export default Contact;