import React, { useRef } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom';

//global components
import Nav from './components/nav';
// import MobileNav from './components/mobileNav'

//pages
import Home from './pages/home';
import About from './pages/about';
import Work from './pages/work';
import Contact from './pages/contact';

//workPages
import Work01 from './pages/work/01';
import Work02 from './pages/work/02';
import Work03 from './pages/work/03';
import Work04 from './pages/work/04';
import Work05 from './pages/work/05';

import './styles/scss/style.scss';


function App() {
  const wrapper = useRef (null)
  const resetScroll = () => wrapper.current?.scrollTo(0,0)
  
  return (
    <Router>
      <div className="App">
        <Nav />
        <div className="pagesWrapper" ref={wrapper}>
          <Switch onUpdate={() => wrapper.current.scrollTo(0, 0)}>
            {/* <Route exact path='/' component={Home} /> */}
            <Route exact path="/" render={() => <Home resetScroll={resetScroll} />} />
            <Route exact path="/portfolio" render={() => <Home resetScroll={resetScroll} />} />
            <Route exact path="/about" render={() => <About resetScroll={resetScroll} />} />
            <Route exact path="/work" render={() => <Work resetScroll={resetScroll} />} />
            <Route exact path="/contact" render={() => <Contact resetScroll={resetScroll} />} />
            <div>
              {/* Work Pages */}
              <Route exact path="/work/01" render={() => <Work01 resetScroll={resetScroll} />} />
              {/* <Route exact path='/work/01' component={Work01} /> */}
              <Route exact path="/work/02" render={() => <Work02 resetScroll={resetScroll} />} />
              <Route exact path="/work/03" render={() => <Work03 resetScroll={resetScroll} />} />
              <Route exact path="/work/04" render={() => <Work04 resetScroll={resetScroll} />} />
              <Route exact path="/work/05" render={() => <Work05 resetScroll={resetScroll} />} />
            </div>
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;